<template>
  <div class="register">
    <loading :isDisplay="isLoading"></loading>
    <div class="back">
      <router-link to="main">
        <img src="../../../static/img/back.png" />
      </router-link>
    </div>
    <div class="register-1">
      <img
        src="https://accountstatic.vivo.com.cn/accountstatic.vivo.com.cn/static/img/logo.3c33210.png.webp"
        alt
      />
      <p>账号注册</p>
    </div>
    <div class="register-2">
      <label for>
        <span>账号</span>
        <input type="text" v-model="user.name" placeholder="请输入账号/用户名" />
      </label>

      <label for>
        <span>密码</span>
        <input type="password" v-model="user.password" placeholder="请输入密码" />
      </label>

      <label for>
        <span>确认密码</span>
        <input type="password" v-model="user.checkPassword" placeholder="确认密码" />
      </label>
    </div>
    <div class="register-3">
      <input type="button" class="btn" @click="regusterUser" value="注册" />
    </div>
    <div class="register-3">
      <router-link
        to="login"
        style="font-size: 0.4rem; border-bottom: 1px solid blue; color: blue"
      >
        <p>返回登录</p></router-link
      >
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import axios from "axios";
import { register } from "../../api/data";
import loading from "../../components/loading.vue";
export default {
  data() {
    return {
      user: {
        name: "",
        password: "",
        checkPassword: "",
      },
      isLoading: "false",
    };
  },
  methods: {
    regusterUser() {
      if (this.user.checkPassword != this.user.password) {
        Toast({
          message: "输入密码不相同",
          duration: 950,
        });
        return false;
      }
      if (this.user.name == "") {
        Toast({
          message: "用户名不能为空",
          duration: 950,
        });
        return false;
      }

      if (this.user.password == "") {
        Toast({
          message: "密码不能为空",
          duration: 950,
        });
        return false;
      }

      this.isLoading = "true";
      setTimeout(() => {
        this.isLoading = "false";

        register(this.user.name, this.user.password)
          .then((res) => {
            console.log(res);
            Toast({
              message: res.msg,
              duration: 950,
            });
            if (res.status == 200) {
              localStorage.setItem("user", JSON.stringify(this.user));
              this.$router.push("/login");
            }
          })
          .catch((err) => {
            console.log(err);
          });

      }, 2000);

    },
  },
  components: {
    loading,
  },
};
</script>

<style lang="less" scoped>
.back {
  padding-left: 20px;
  padding-top: 10px;
  img {
    width: 0.8rem;
  }
}
.register {
  width: 100%;
  height: 100%;
  position: fixed;
  background: white;
  left: 0;
  top: 0;
}
.register-1 {
  margin-top: 2rem;
}
.register-1 img {
  width: 3rem;
  display: block;
  margin: 0.6rem auto;
}
.register-1 p {
  color: black;
  text-align: center;
  font-size: 0.5rem;
}

.register-2 {
  margin-top: 0.6rem;
  margin-bottom: 0.6rem;
}

.register-2 label {
  width: 90%;
  margin: auto;
  height: 1.5rem;
  border-bottom: 0.03rem solid #f2f2f2;
  display: flex;
}

.register-2 label span {
  width: 20%;
  font-size: 0.4rem;
  line-height: 1.5rem;
  display: block;
  float: left;
}

.register-2 label input {
  width: 80%;
  font-size: 0.4rem;
  padding-left: 0.3rem;
}

.register-3 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  width: 9rem;
  height: 1.1rem;
  margin: 0 auto;
  margin-bottom: 0.3rem;
  text-align: center;
  line-height: 1.1rem;
  font-size: 0.45rem;
  color: #fff;
  border-radius: 0.6rem;
  background-color: #46a1fa;
  background-image: linear-gradient(90deg, #418eff, #4566ff);
}
</style>



