<template>
  <div class="content">
    <v-header title="v友大厅"></v-header>
    <div v-if="!islogin" class="room">
      <div class="sendbody">
        <div class="online" @click="showUserList">
          <img src="../../../static/img/箭头.png" class="showUserImg" />
          <p>{{ online }}人在线</p>
        </div>
        <div class="userList" :style="{ height: userListBlockWidth ? 'auto' : 0 }">
          <ul>
            <li v-for="item in loginUser">{{ item }}</li>
          </ul>
        </div>
        <div
          v-for="(item, index) in informationList"
          :key="index"
          :class="nameinput == item.name ? 'right' : 'left'"
          class="infolist"
        >
          <!-- <div
            class="msgImg"
            v-if="item.id != userid"
            :style="{ background: item.color }"
          ></div> -->
          <div class="msgContent">
            <!-- <div class="item_name" v-if="item.id != userid">
              {{ item.name }}
            </div> -->
            <div
              class="item_name"
              :class="nameinput == item.name ? 'right' : 'left'"
              :style="{ color: item.color }"
            >
              {{ item.name }}
            </div>
            <div
              class="item_msg"
              :class="nameinput == item.name ? 'right' : 'left'"
              :style="{ background: item.color }"
            >
              {{ item.msg }}
            </div>
          </div>
          <!-- <div
            class="msgImg"
            v-if="item.id == userid"
            :style="{ background: item.color }"
          ></div> -->
        </div>
      </div>
      <div class="send">
        <input
          type="text"
          value=""
          v-model="cont"
          @keyup.enter="sendmsg()"
          placeholder="请输入要发送的内容"
        />
        <button
          type="default"
          class="sendbtn"
          @click="sendmsg()"
          @keyup.enter="sendmsg()"
        >
          发送
        </button>
      </div>
    </div>

    <div v-if="islogin">
      <div class="login">
        <input
          type="text"
          value=""
          placeholder="请输入您的代号"
          class="login_input"
          v-model="nameinput"
        />

        <div class="checkColor">
          <ul>
            <li
              v-for="(item, index) in ColorList"
              :key="index"
              :style="{
                background: item,
                'box-shadow': checkColorIndex == index ? examShadow : 'none',
              }"
              @click="activeColor(item, index)"
            ></li>
          </ul>
        </div>
        <p class="checkColorTitle">挑选你的色调</p>
        <button
          type="default"
          class="login_btn"
          @click="toroom()"
          :style="{ background: checkColor }"
        >
          进入聊天室
        </button>
      </div>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
import footer from "../../components/footer/index.vue";
import header from "../../components/header";
import { Toast } from "mint-ui";

export default {
  data() {
    return {
      islogin: true,
      server: "",
      nameinput: "",
      userid: "1",
      cont: "",
      online: 0,
      informationList: [],
      ColorList: ["red", "yellow", "green", "skyblue", "pink", "blue"],
      checkColor: "red",
      otherUserColor: "red",
      checkColorIndex: "",
      examShadow: "0px 0px 10px 4px red",
      loginUser: [],
      userListBlockWidth: true,
    };
  },
  mounted() {
    if (this.$store.state.isLoginSocket) {
      this.islogin = false;
    }
    this.userid = this.$store.state.ChatID;
    this.informationList = this.$store.state.ChatInfo;
    this.nameinput = this.$store.state.ChatName;
    this.loginUser = this.$store.state.loginUser;

    console.log(this.informationList);
    window.addEventListener("beforeunload", (e) => this.beforeunloadHandler(e));
  },
  methods: {
    beforeunloadHandler(e) {
      this.$socket.emit("haveUserLeave", this.nameinput);
    },
    toroom() {
      if (this.nameinput == "") {
        Toast({
          message: "请正确输入您的代号！",
          duration: 2000,
        });
        return;
      }
      if (this.nameinput.length > 6) {
        Toast({
          message: "您的代号不能高于6位！",
          duration: 2000,
        });
        return;
      }
      this.$store.state.ChatID = this.userid;
      this.$store.state.ChatName = this.nameinput;
      this.$store.state.isLoginSocket = !this.$store.state.isLoginSocket;
      this.$store.state.loginUser.push(this.nameinput);
      this.loginUser = this.$store.state.loginUser;
      // this.loginUser.push(this.nameinput);
      this.$socket.emit("connection", 1);
      this.$socket.emit("login", this.nameinput);
      // this.$socket.emit("ToastIn", this.nameinput);
      this.$socket.emit("");

      this.islogin = !this.islogin;

      setInterval(() => {
        this.$socket.emit("online");
      }, 1000);
    },
    sendmsg() {
      if (this.cont != "") {
        let data = {
          color: this.checkColor,
          name: this.nameinput,
          msg: this.cont,
          id: this.userid,
        };
        this.$store.state.ChatInfo.push(data);
        this.informationList = this.$store.state.ChatInfo;
        // this.informationList.push(data);
        this.$socket.emit("message", data);
        this.cont = "";
      }
    },
    activeColor(item, index) {
      this.checkColor = this.ColorList[index];
      // console.log(this.checkColor);
      this.checkColorIndex = index;
      this.examShadow = `0px 0px 10px 4px ${this.ColorList[index]}`;
    },
    showUserList() {
      this.userListBlockWidth = !this.userListBlockWidth;
    },
  },

  sockets: {
    connect() {
      console.log("socket.io链接成功");
      // 向服务端发消息
      // this.sockets.emit("ClientReceive", "socket.io连接成功后向后台发送消息");
    },
    disconnect() {
      console.log("socket.io断开链接");
      this.$socket.emit("OutToastIn", this.nameinput);
    }, //检测socket断开链接
    reconnect() {
      console.log("socket.io重新链接");
    },
    online(msg) {
      // console.log("人数", msg);
      this.online = msg;
    },
    message(data) {
      this.otherUserColor = data.color;
      this.$store.state.ChatInfo.push(data);
      this.informationList = this.$store.state.ChatInfo;
      // this.informationList.push(data);
    },
    login(data) {
      if (!data.id) {
        Toast({
          message: data.msg,
          duration: 2000,
        });
        this.islogin = !this.islogin;

        return;
      }
      this.$socket.emit("ToastIn", this.nameinput);

      this.userid = data.id;
      this.loginUser = data.loginUserList;
    },
    ToastIn(data) {
      this.$store.state.loginUser = data.loginUser;
      this.loginUser = this.$store.state.loginUser;

      // this.loginUser.push(data);
      // console.log(this.loginUser);
      if (this.islogin != true) {
        Toast({
          message: `${data.msg}加入了房间`,
          duration: 2000,
        });
      }
    },
    haveUserLeave(msg) {
      for (let i = 0; i < this.loginUser.length; i++) {
        if (this.loginUser[i] == msg) {
          this.loginUser.splice(i, 1);
        }
      }
      //  this.loginUser.push(data);
      // console.log(this.loginUser);
      if (this.islogin != true) {
        Toast({
          message: `${msg}退出了房间`,
          duration: 2000,
        });
      }
    },
  },
  components: {
    "v-footer": footer,
    "v-header": header,
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}

.content {
  height: 100vh;
  width: 100vw;
}

.sendbody {
  height: 86vh;
  width: 100vw;
  background-color: rgba(149, 149, 149, 0.1);
  overflow-y: scroll;
  overflow-x: hidden;
  .online {
    color: rgba(0, 170, 255, 0.8);
    font-weight: 900;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: flex-start;
    padding: 1.5rem 0 0 0;
    background-color: white;
    padding-left: 15px;

    .showUserImg {
      width: 0.6rem;
      height: 0.6rem;
      transform: rotate(180deg);
      animation: showUserListAnimate 1s infinite;
    }
    @keyframes showUserListAnimate {
      0% {
        transform: translateY(0px) rotate(180deg);
      }
      33% {
        transform: translateY(-3px) rotate(180deg);
      }
      66% {
        transform: translateY(3px) rotate(180deg);
      }
      100% {
        transform: translateY(0px) rotate(180deg);
      }
    }
    span {
      background-color: gray;
      color: white;
      padding: 5px 10px;
      border-radius: 15px;
    }
    p {
      font-size: 0.5rem;
      margin: 0 8px 0 0;
    }
  }
  .userList {
    height: 1rem;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    overflow-y: hidden;
    overflow-x: scroll;
    transition: 0.3s all;
    ul {
      display: flex;
      transition: 0.3s;
      li {
        transition: 0.3s;

        font-size: 0.6rem;
        border: 1px solid rgb(255, 255, 255);
        margin: 5px 5px;
        padding: 0 2px;
        border-radius: 7px;
        background-color: skyblue;
        color: white;
      }
    }
  }
  .infolist {
    width: 100%;
    display: flex;
    margin-bottom: 10px;
    .msgImg {
      width: 1rem;
      height: 1rem;
      // background-color: gold;
      margin: 10px;
      border-radius: 50%;
    }
    .msgContent {
      width: auto;
      height: auto;
      display: flex;
      flex-direction: column;
      margin: 0 10px;
    }
    .item_name {
      width: auto;
      // max-width: 100%;
      font-size: 0.5rem;
      font-weight: 600;
      margin-bottom: 5px;
      padding: 0 5px;
    }

    .item_msg {
      width: auto;
      background: rgba(255, 255, 255, 1);
      border-radius: 5px;
      font-size: 0.5rem;
      padding: 3px 10px;
      margin: 0 5px;
      color: white;
      word-wrap: break-word;
      text-align: left !important;
    }
  }
}
.left {
  justify-content: flex-start;
  text-align: left;
}

.right {
  justify-content: flex-end;
  text-align: right;

  .item_msg {
    background-color: rgba(85, 170, 255, 0.3);
  }
}

.send {
  // border: 1px solid gray;
  height: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.5rem;
  position: fixed;
  bottom: 1.65rem;

  input {
    width: 80vw;
    // background: rgba(255, 255, 255, 0.1);
    border: 2px solid rgba(0, 170, 255, 0.4);
    // box-shadow: 0px 0px 5px 5px rgba(85, 170, 255, 0.3);
    height: 1.2rem;
    border-radius: 3px;
  }

  .sendbtn {
    width: 20vw;
    height: 1.2rem;
    flex: 1;
    margin-right: 3px;
    background-color: rgba(85, 170, 255, 0.6);
    color: white;
    font-weight: 900;
    // box-shadow: 0px 0px 10px 10px rgba(85, 170, 255, 0.3);
    border: none;
  }
}

.login {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .login_input {
    background-color: rgba(0, 0, 0, 0.1);
    width: 70%;
    height: 1.1rem;
    border-radius: 50px;
    font-size: 0.5rem;
    text-align: center;
  }

  .login_btn {
    // background-color: skyblue;
    border-radius: 50px;
    padding: 10px;
    height: auto;
    margin-top: 50px;
    color: black;
    font-weight: 900;
    font-size: 0.4rem;
    border: none;
    color: white;
    transition: 0.5s all;
  }
}
.checkColor {
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 60px;
  // background-color: skyblue;
  margin-top: 50px;
  ul {
    display: flex;
    flex-direction: row;
    li {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      // background-color: red;
      margin: 0 3px;
      transition: 0.5s all;
    }
  }
}
.checkColorTitle {
  font-size: 0.4rem;
  font-weight: 900;
}
</style>



