<template>
  <div class="register">
    <loading :isDisplay="isLoading"></loading>
    <div class="back" @click="toMain">
      <img src="../../../static/img/back.png" />
    </div>

    <div class="register-1">
      <img
        src="https://accountstatic.vivo.com.cn/accountstatic.vivo.com.cn/static/img/logo.3c33210.png.webp"
        alt
      />
      <p>账号登陆</p>
    </div>
    <div class="register-2">
      <label for>
        <span>账号</span>
        <input type="text" v-model="user.name" placeholder="请输入账号/用户名" />
      </label>

      <label for>
        <span>密码</span>
        <input type="password" v-model="user.password" placeholder="请输入密码" />
      </label>
    </div>
    <div class="register-3">
      <input type="button" class="btn toRegister" @click="login" value="登陆" />
    </div>
    <div class="register-3">
      <router-link
        to="register"
        style="font-size: 0.4rem; border-bottom: 1px solid blue; color: blue"
      >
        <p>去注册</p></router-link
      >
    </div>

  </div>
</template>

<script>
import { Toast } from "mint-ui";
import { login } from "../../api/data";
import loading from "../../components/loading.vue";
import router from "../../router";
import footer from '../../components/footer'
export default {
  data() {
    return {
      user: {
        name: "",
        password: "",
      },
      isLoading: "false",
    };
  },
  mounted() {},
  methods: {
    login() {
      if (this.user.name == "") {
        Toast({
          message: "请输入用户名",
          duration: 950,
        });
        return false;
      }

      if (this.user.password == "") {
        Toast({
          message: "请输入密码",
          duration: 950,
        });
        return false;
      }

      this.isLoading = "true";
      setTimeout(() => {
        this.isLoading = "false";

        login(this.user.name, this.user.password).then((res) => {
          Toast({
            message: res.msg,
            duration: 950,
          });
          if (res.status == 200) {
            localStorage.setItem("user", JSON.stringify(this.user));
            this.$router.push("/main");
          }
        });
      }, 2000);
    },

    jumpRegister() {
      this.$router.push("/register");
    },
    toMain() {
      router.push({ name: "index" });
    },
  },
  components: {
    loading,
  },
};
</script>

<style lang="less">
.back {
  padding-left: 20px;
  padding-top: 10px;
  img {
    width: 0.8rem;
  }
}
.register {
  width: 100%;
  height: 100%;
  position: fixed;
  background: white;
  left: 0;
  top: 0;
}
.register-1 {
  margin-top: 2rem;
}
.register-1 img {
  width: 3rem;
  display: block;
  margin: 0.6rem auto;
}
.register-1 p {
  color: black;
  text-align: center;
  font-size: 0.5rem;
}
.register-2 {
  /* display: flex;
  flex-direction: column; */
  margin-top: 0.6rem;
  margin-bottom: 0.6rem;
}
.register-2 label {
  width: 90%;
  margin: auto;
  height: 1.5rem;
  border-bottom: 0.03rem solid #f2f2f2;
  display: flex;
}
.register-2 label span {
  width: 20%;
  font-size: 0.4rem;
  line-height: 1.5rem;
  display: block;
  float: left;
}

.register-2 label input {
  width: 80%;
  font-size: 0.4rem;
  padding-left: 0.3rem;
}
.register-3 {
  display: flex;
  justify-content: center;
}
.btn {
  width: 9rem;
  height: 1.1rem;
  margin: 0 auto;
  /* margin-top: 0.72rem; */
  margin-bottom: 0.3rem;
  text-align: center;
  line-height: 1.1rem;
  font-size: 0.45rem;
  color: #fff;
  border-radius: 0.6rem;
  background-color: #46a1fa;
  background-image: linear-gradient(90deg, #418eff, #4566ff);
}
// .toRegister {
//   font-size: 0.1rem !important;
// }
</style>



