<template>
  <div class="top" v-if="JSON.parse(isDisplay)">
    <img src="../../static/img/加载.png" class="loading" />
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {},
  props: {
    isDisplay: String,
  },
};
</script>

<style lang="less" scoped>
.top {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 9999999;
  user-select: none;
}
.loading {
  width: 0.9rem;
  animation: loading 2s linear infinite;
}
@keyframes loading {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>



