<template>
  <div class="top">
    <v-header title="搜索详情" class="header" headerLeftStatus="true"></v-header>
    <div class="main">
      <div class="main-box">
        <ul>
          <li v-for="(v, i) in DataList" :key="i" @click="jumpDetail(1, v)">
            <div class="list">
              <div class="image">
                <img :src="v.img_url" alt="图片" />
              </div>
              <p class="name">{{ v.name }}</p>
              <p class="nametwo">{{ v.content }}</p>
              <p class="price">￥ {{ toFixed(v.price) }}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import header from "../../components/header";
export default {
  data() {
    return {
      DataList: [],
    };
  },
  methods: {
    jumpDetail(list, v) {
      this.$router.push({
        path: "detail",
        query: {
          id: v.id,
          shop_id: v.shop_id,
        },
      });
    },
    toFixed(value) {
      return JSON.parse(value).toFixed(2);
    },
  },
  mounted() {
    if (localStorage.getItem("searchList")) {
      this.DataList = JSON.parse(localStorage.getItem("searchList"));
      console.log(this.DataList);
    }
  },
  components: {
    "v-header": header,
  },
};
</script>

<style lang="less" scoped>
.icon {
  width: 100%;
  height: 2rem;
  background: white;
  border-bottom: 1px solid #f4f4f4;
  .icon-ul {
    width: 100%;
    height: 100%;
    .icon-li {
      width: 25%;
      height: 100%;
      float: left;
      margin: auto;
      a {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: block;
          width: 70%;
        }
      }
    }
  }
}
.main {
  margin-bottom: 1.6rem;
  position: relative;
  top: 50px;
  h2 {
    display: block;
    height: 1.6rem;
    background: #f7f7f7;
    font-size: 0.49rem;
    line-height: 1.6rem;
    color: #333;
    text-align: center;
  }
  h2::before,
  h2::after {
    position: relative;
    margin: 0 0.12rem;
    display: inline-block;
    content: " ";
    height: 1px;
    width: 0.4rem;
    background-color: #9c9c9c;
    top: -0.16rem;
  }
  ul {
    width: 100%;
    height: auto;
    overflow: hidden;
  }
  li {
    list-style: none;
    .list {
      width: 50%;
      height: auto;
      background: white;
      float: left;
      border-right: 1px solid #f4f4f4;
      border-top: 1px solid #f4f4f4;
      padding-bottom: 0.25rem;
      .price {
        margin: auto;
        text-align: center;
        font-size: 0.38rem;
        color: #f81200;
        font-weight: 500;
        padding-top: 0.8rem;
        padding-bottom: 0.2rem;
      }
      .name {
        width: 80%;
        height: 0.64rem;
        line-height: 0.5rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin: auto;
        font-size: 0.38rem;
        font-weight: 800;
        text-align: center;
      }
      .nametwo {
        width: 90%;
        height: 0.99rem;
        font-size: 0.29rem;
        text-align: center;
        overflow: hidden;
        margin: auto;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
      }
      .image {
        width: 100%;
        background: white;
        padding-top: 0.2rem;
        padding-bottom: 0.3rem;
        img {
          width: 2.48rem;
          height: 2.6rem;
          display: block;
          margin: auto;
          margin-top: 0.4rem;
          margin-bottom: 0.2rem;
        }
      }
    }
  }
}
</style>



