export function getData() {
  return http.axios_get('http://47.96.151.77:3000/') //
}
export function register(name,pass){
  return http.axios_get(`http://47.96.151.77:3000/register?name=${name}&&pass=${pass}`) //
}
export function login(name,pass){
  return http.axios_get(`http://47.96.151.77:3000/login?name=${name}&&pass=${pass}`) //
}



