<template>
  <div class="home">
    <div class="header">
      <i class="iconfont icon-VIVO"></i>
    </div>
    <div class="official"></div>
    <input
      type="text"
      v-model="search"
      placeholder="请输入想搜索的机型"
      @keyup.enter="toSearchDetail(searchList)"
      class="search"
    />
    <div class="searchList">
      <ul>
        <li v-for="item in searchList" @click="toDetail(item.id, item.shop_id)">
          {{ item.name }}
        </li>
      </ul>
    </div>
    <div class="swiper">
      <mt-swipe :auto="2000">
        <mt-swipe-item>
          <img
            src="https://shopstatic.vivo.com.cn/vivoshop/commodity/20180418/20180418104131830678_original.jpg"
          />
        </mt-swipe-item>
        <mt-swipe-item>
          <img
            src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2F0xzx.com%2Fwp-content%2Fuploads%2F2021%2F05%2FvivoZEISS_720.jpg&refer=http%3A%2F%2F0xzx.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1657185708&t=74839aaa67dcb98e54538bc8289ad0c2"
          />
        </mt-swipe-item>
        <mt-swipe-item>
          <img
            src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg.shangyexinzhi.com%2Fxztest-image%2Farticle%2F2789ef1aede960eb86d2fd42b8812eca.jpeg%3Fx-oss-process%3Dimage%2Fresize%2Cw_670&refer=http%3A%2F%2Fimg.shangyexinzhi.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1657185755&t=7742e380cc1689661331e09938fda0a6"
          />
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="icon">
      <ul class="icon-ul">
        <li class="icon-li" v-for="(list, index) in routers" :key="index">
          <router-link :to="{ path: list.src }">
            <img :src="list.img" alt="vivo" />
          </router-link>
        </li>
      </ul>
    </div>
    <div class="main">
      <div class="main-box" v-for="(list, index) in shopListData" :key="index">
        <h2>{{ list.title }}</h2>
        <ul>
          <li v-for="(v, i) in list.data" :key="i" @click="jumpDetail(list, v)">
            <div class="list">
              <div class="image">
                <img :src="v.img_url" alt="图片" />
              </div>
              <p class="name">{{ v.name }}</p>
              <p class="nametwo">{{ v.content }}</p>
              <p class="price">￥ {{ toFixed(v.price) }}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <v-footer></v-footer>
  </div>
</template>
<script>
import { getData } from "@/api/data";
import footer from "@/components/footer/index";
export default {
  name: "index",
  data() {
    return {
      routers: [
        {
          img: "./static/img/xuangou.jpg",
          src: "/phone",
        },
        {
          img: "./static/img/peijian.jpg",
          src: "/parts",
        },
        {
          img: "./static/img/pingbao.jpg",
          src: "/",
        },
        {
          img: "./static/img/dingzhi.jpg",
          src: "/",
        },
      ],
      shopListData: [],
      phoneData: [], //手机
      accessoriesData: [], //配件
      searchList: [],
      search: "",
    };
  },
  methods: {
    // 首页推荐跳转
    jumpDetail(list, v) {
      this.$router.push({
        path: "detail",
        query: {
          id: v.id,
          shop_id: list.id,
        },
      });
    },
    // 搜索结果跳转
    toDetail(id, shopId) {
      this.$router.push({
        path: "detail",
        query: {
          id: id,
          shop_id: shopId,
        },
      });
    },
    toFixed(value) {
      return JSON.parse(value).toFixed(2);
    },
    homeShopListData() {
      getData().then((res) => {
        console.log(res);
        this.shopListData = res.homeData;
        this.phoneData = res.phone[0].data;
        this.accessoriesData = res.accessories[0].data;
      });
    },
    toSearchDetail(data) {
      localStorage.setItem("searchList", JSON.stringify(data));
      this.$router.push({ path: "searchDetail" });
    },
  },
  mounted() {
    this.homeShopListData();
  },
  watch: {
    search: {
      handler(newval, oldval) {
        this.searchList = [];
        if (!newval) {
          return (this.searchList = []);
        }
        this.phoneData.filter((item) => {
          if (item.name.indexOf(newval) != -1) {
            this.searchList.push(item);
          }
        });

        this.accessoriesData.filter((item) => {
          if (item.name.indexOf(newval) != -1) {
            this.searchList.push(item);
          }
        });
        if (this.searchList.length == 0) {
          return (this.searchList = [{ name: "暂无更多" }]);
        }
        console.log(this.searchList);
      },
    },
  },
  components: {
    "v-footer": footer,
  },
};
</script>

<style lang="less" scoped>
.home {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  // align-items: center;
  .header {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1;
    height: 1.45rem;
    line-height: 1.45rem;
    font-size: 0.35rem;
    background: black;
    i {
      font-size: 0.55rem;
      color: white;
      text-align: center;
      display: block;
    }
  }
  .official {
    width: 100%;
    height: 0.8rem;
    background: white;
    margin-top: 1.45rem;
    background: url("/static/img/official.png") no-repeat;
    background-size: 100% 100%;
  }
  .swiper {
    margin-top: 1px;
    height: 6.9rem;
    img {
      width: 100%;
      height: 6.9rem;
    }
  }
  .icon {
    width: 100%;
    height: 2rem;
    background: white;
    border-bottom: 1px solid #f4f4f4;
    .icon-ul {
      width: 100%;
      height: 100%;
      .icon-li {
        width: 25%;
        height: 100%;
        float: left;
        margin: auto;
        a {
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            display: block;
            width: 70%;
          }
        }
      }
    }
  }
  .main {
    margin-bottom: 1.6rem;
    h2 {
      display: block;
      height: 1.6rem;
      background: #f7f7f7;
      font-size: 0.49rem;
      line-height: 1.6rem;
      color: #333;
      text-align: center;
    }
    h2::before,
    h2::after {
      position: relative;
      margin: 0 0.12rem;
      display: inline-block;
      content: " ";
      height: 1px;
      width: 0.4rem;
      background-color: #9c9c9c;
      top: -0.16rem;
    }
    ul {
      width: 100%;
      height: auto;
      overflow: hidden;
    }
    li {
      list-style: none;
      .list {
        width: 50%;
        height: auto;
        background: white;
        float: left;
        border-right: 1px solid #f4f4f4;
        border-top: 1px solid #f4f4f4;
        padding-bottom: 0.25rem;
        .price {
          margin: auto;
          text-align: center;
          font-size: 0.38rem;
          color: #f81200;
          font-weight: 500;
          padding-top: 0.8rem;
          padding-bottom: 0.2rem;
        }
        .name {
          width: 80%;
          height: 0.64rem;
          line-height: 0.5rem;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          margin: auto;
          font-size: 0.38rem;
          font-weight: 800;
          text-align: center;
        }
        .nametwo {
          width: 90%;
          height: 0.99rem;
          font-size: 0.29rem;
          text-align: center;
          overflow: hidden;
          margin: auto;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
        }
        .image {
          width: 100%;
          background: white;
          padding-top: 0.2rem;
          padding-bottom: 0.3rem;
          img {
            width: 2.48rem;
            height: 2.6rem;
            display: block;
            margin: auto;
            margin-top: 0.4rem;
            margin-bottom: 0.2rem;
          }
        }
      }
    }
  }
}
.search {
  text-align: center;
  width: 100%;
  height: 1rem;
  padding: 0;
  font-size: 0.4rem;
  // box-shadow: 3px 3px 5px rgb(149, 172, 235);
  z-index: 999;
  background-color: rgb(238, 238, 247);
  color: black;
}
.searchList {
  width: 100%;
  height: auto;
  position: absolute;
  top: 3.25rem;
  z-index: 999;
  background-color: rgb(255, 255, 255);
  transition: 0.1s all;
  ul {
    list-style-type: none;
    li {
      height: 60px;
      font-size: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.3s all;
    }
    li:hover {
      background-color: rgb(223, 223, 223);
    }
  }
}
</style>



